@font-face{font-family:'HelveticaNeueCyr-Light';src:url("/fonts/HelveticaNeueCyr-Light.ttf")}@font-face{font-family:'HelveticaNeueCyr-Thin';src:url("/fontsHelveticaNeueCyr-Thin.ttf")}@font-face{font-family:'HelveticaNeueCyr-Medium';src:url("/fonts/HelveticaNeueCyr-Medium.ttf")}html{width:100%;height:100%}*{font-family:'HelveticaNeueCyr-Light', sans-serif}@media (min-width: 320px){html{font-size:50.1%}}@media (min-width: 768px){html{font-size:50.1%}}@media (min-width: 992px){html{font-size:56.5%}}@media (min-width: 1200px){html{font-size:56.5%}}body{width:100%;height:100%;font-size:1.6rem}body ::-webkit-scrollbar{width:9px;background-color:rgba(146,146,146,0)}body ::-webkit-scrollbar-track{border-radius:4px}body ::-webkit-scrollbar-thumb{border-radius:4px;background-color:rgba(180,180,180,0.8)}body ::-webkit-scrollbar-thumb:hover{background-color:rgba(130,130,130,0.6);cursor:pointer}body ::-webkit-scrollbar-thumb:active{background-color:rgba(130,130,130,0.9);cursor:pointer}body input[type=number]::-webkit-inner-spin-button,body input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}body input:focus::-webkit-contacts-auto-fill-button{display:none !important}
